import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { Heading, Body, Caption, Anchor, Button } from 'legion-ui';
import Moment from 'react-moment';
import 'moment-timezone';
// require('dotenv').config();

// var sort = "";
const ResearchList = () => {
  const [researches, setResearch] = useState([]);
  const [products, setProduct] = useState([]);
  const [tribes, setTribe] = useState([]);
  const [methods, setMethods] = useState([]);
  const [outputs, setOutputs] = useState([]);

  const [filterTribe, setfilterTribe] = useState({});
  const [filterProduct, setfilterProduct] = useState({});
  const [filterMethod, setfilterMethod] = useState({});
  const [filterOutput, setfilterOutput] = useState({});
  const [filterKeywords, setfilterKeywords] = useState({});
  const [sort, setSort] = useState("");
  useEffect(() => {
    getResearches();
  }, [filterTribe, filterProduct, filterMethod, filterOutput, filterKeywords, sort]);
  useEffect(() => {
    getResearches();
    getProducts();
    getTribes();
    getMethods();
    getOutputs();
  }, []);

  //TODO: Sort based on state change to element
  const handleSort = (e) => {
    var el = document.getElementById(`${e.target.name}Sort`);
    if (e.target.name === "name") {
      if (sort === "Name ASC") {
        el.classList.remove("fa-arrow-down-a-z");
        el.classList.add("fa-arrow-up-a-z");
        setSort("Name DESC");
      } else {
        setSort("Name ASC");
        el.classList.remove("fa-arrow-up-a-z");
        el.classList.add("fa-arrow-down-a-z");
      }
    } else { //date
      if (sort === "Date ASC") {
        el.classList.remove("fa-arrow-down-9-1");
        el.classList.add("fa-arrow-up-9-1");
        setSort("Date DESC");
      } else {
        setSort("Date ASC");
        el.classList.remove("fa-arrow-up-9-1");
        el.classList.add("fa-arrow-down-9-1");
      }
    }
  }
  function resetFilter() {
    document.getElementById(`tribeFilter`).classList.add("d-none");
    document.getElementById(`productFilter`).classList.add("d-none");
    document.getElementById(`methodFilter`).classList.add("d-none");
    document.getElementById(`outputFilter`).classList.add("d-none");
    document.getElementById(`keywordsFilter`).classList.add("d-none");
    setfilterTribe({});
    setfilterProduct({});
    setfilterMethod({});
    setfilterOutput({});
    setfilterKeywords({});
  }
  const handleChange = (e) => {
    const el = document.getElementById(`${e.target.name}Filter`);
    el.classList.remove("d-none");
    const el3 = document.getElementById(`filteredBy`);
    el3.classList.remove("d-none");
    el3.classList.add("d-inline");
    if (e.target.name === "keywords") {
      var data = {
        type: e.target.name,
        value: e.target.value,
        text: e.target.value
      }
    } else {
      var data = {
        type: e.target.name,
        value: e.target.value,
        text: e.nativeEvent.target[e.nativeEvent.target.selectedIndex].text
      }
    }

    if (data.value === '') {
      data = {};
      el.classList.add("d-none");
    }
    if (e.target.name === "tribe") {
      setfilterTribe(data);
    } else if (e.target.name === "product") {
      setfilterProduct(data);
    } else if (e.target.name === "method") {
      setfilterMethod(data);
    } else if (e.target.name === "output") {
      setfilterOutput(data);
    } else if (e.target.name === "keywords") {
      setfilterKeywords(data);
    }
  }
  const getResearches = async () => {

    const response = await axios.put(`${process.env.REACT_APP_API_URL}/researches`, {
      TribeID: filterTribe,
      ProductID: filterProduct,
      ResearchOutputID: filterOutput,
      ResearchMethodID: filterMethod,
      Keywords: filterKeywords,
      Sort: sort,
    });
    setResearch(response.data);
  };
  const getProducts = async () => {
    const response = await axios.get(`${process.env.REACT_APP_API_URL}/products`);
    setProduct(response.data);
  };
  const getTribes = async () => {
    const response = await axios.get(`${process.env.REACT_APP_API_URL}/tribes`);
    setTribe(response.data);
  };
  const getMethods = async () => {
    const response = await axios.get(`${process.env.REACT_APP_API_URL}/methods`);
    setMethods(response.data);
  };
  const getOutputs = async () => {
    const response = await axios.get(`${process.env.REACT_APP_API_URL}/outputs`);
    setOutputs(response.data);
  };


  return (
    <>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12 mt-2">
            <div className="card">
              <div className="card-body">
                <div className="row">
                  <div className="col-6">
                    <Heading size="h2">Research Repository</Heading>
                    <Body variant="sm_reguler">Research Repository of Telkom Indonesia’s digital products.</Body>
                    <Link to={`/add`} className="d-block button is-success">Add New </Link>
                  </div>
                  <div className="col-6">
                    <img src={'/assets/images/RH-03.png'} className="w-50 pull-right d-block" />
                  </div>
                </div>
                <div className="row">
                  <div className="col-12">
                    <div className="form-group">
                      <input id="keywords" name="keywords" onChange={handleChange} type="text" className="form-control" placeholder="Search by keywords..." />
                    </div>
                  </div>
                  <div className="col-3">
                    <div className="form-group">
                      <select findby="tribe" name="tribe" className="form-control" id="tribe"
                        onChange={handleChange} >
                        <option value="" disabled selected hidden>Tribe</option>
                        <option value="">All Tribe</option>
                        {tribes.map((tribe, index) => (
                          <option value={tribe.TribeID}>{tribe.TribeID}</option>
                        )
                        )}
                      </select>
                    </div>
                  </div>
                  <div className="col-3">
                    <div className="form-group">
                      <select findby="product" name="product" className="form-control" id="product"
                        onChange={handleChange} >
                        <option value="" disabled selected hidden>Product</option>
                        <option value="">All Product</option>
                        {products.map((product, index) => (
                          <option value={product.ProductID}>{product.ProductName}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="col-3">
                    <div className="form-group">
                      <select findby="method" name="method" className="form-control" id="method"
                        onChange={handleChange} >
                        <option value="" disabled selected hidden>Research method</option>
                        <option value="">All Method</option>
                        {methods.map((method, index) => (
                          <option value={method.ResearchMethodID}>{method.ResearchMethodName}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="col-3">
                    <div className="form-group">
                      <select findby="output" name="output" className="form-control" id="output"
                        onChange={handleChange} >
                        <option value="" disabled selected hidden>Output</option>
                        <option value="">All Output</option>
                        {outputs.map((output, index) => (
                          <option value={output.ResearchOutputID}>{output.ResearchOutputName}</option>
                        ))}
                      </select>
                    </div>
                  </div>

                </div>
              </div>
            </div>
            <div className="row mt-3">
              <div className="col-8">
                <div id="filteredBy" className="mr-1 d-none"><Anchor onClick={() => resetFilter()}>Reset All Filter</Anchor> </div>
                <button className="mr-1 btn btn-light btn-sm d-none font-weight-normal" id="tribeFilter">{filterTribe.text}</button>
                <button className="mr-1 btn btn-light btn-sm d-none font-weight-normal" id="productFilter">{filterProduct.text}</button>
                <button className="mr-1 btn btn-light btn-sm d-none font-weight-normal" id="methodFilter">{filterMethod.text}</button>
                <button className="mr-1 btn btn-light btn-sm d-none font-weight-normal" id="outputFilter">{filterOutput.text}</button>
                <button className="mr-1 btn btn-light btn-sm d-none font-weight-normal" id="keywordsFilter">{filterKeywords.text}</button>
              </div>
              <div className="col-4">
                <Button size="sm" variant="outline" type="button" name="date" onClick={handleSort} className="mr-1 font-weight-normal pull-right"><i id="dateSort" className="fa-solid fa-arrow-up-9-1"></i> Date</Button>
                <Button size="sm" variant="outline" type="button" name="name" onClick={handleSort} className="mr-1 font-weight-normal pull-right"><i id="nameSort" className="fa-solid fa-arrow-down-a-z"></i> Name</Button>
                <label id="sortBy" className="mr-1 pull-right">Sort by: </label>

                {/* <i className="fa-solid fa-arrow-up-a-z"></i> */}
              </div>
            </div>

            <div className="content-row">
              <div className="row" id="researchlist">
                {researches.map((research, index) => (
                  <>
                    <div key={research} className="col-4 nounderline">
                      <div className="card card-hover mt-3">
                        <Link to={`/research/${research.ResearchID}`}>
                          <div className="card-body">
                            <div className="row">
                              <div className="col-3">
                                <img className="w-100 rounded-circle" style={{ position: "relative" }} src=
                                  // {research.product?.ProductLogo}
                                  "https://upload.wikimedia.org/wikipedia/commons/1/14/No_Image_Available.jpg?20200913095930"
                                ></img>
                              </div>
                              <div className="col-9 pl-0">
                                <div style={{ maxHeight: 45, display: "-webkit-box", WebkitLineClamp: 2, WebkitBoxOrient: "vertical", overflow: "hidden", textOverflow: "ellipsis", fontWeight: 600 }}>
                                  {research.ResearchName}
                                </div>
                                <div className="small text-muted" style={{ maxHeight: 45, display: "-webkit-box", WebkitLineClamp: 4, WebkitBoxOrient: "vertical", overflow: "hidden", textOverflow: "ellipsis" }}>
                                  <Moment format="MMM YYYY">
                                    {research.createdAt}
                                  </Moment> | {research.product?.ProductName} | {research.product?.tribe?.TribeName} ({research.product?.tribe?.TribeID})
                                </div>
                              </div>

                            </div>
                            <div className="mt-3 small text-muted" style={{ height: 55, display: "-webkit-box", WebkitLineClamp: 3, WebkitBoxOrient: "vertical", overflow: "hidden", textOverflow: "ellipsis" }}>
                              {research.ResearchDescription.replace(/(<([^>]+)>)/ig, '')}
                            </div>
                            <div className="row">
                              <div className="col-8">
                                <div className="mt-2" style={{ height: 20, display: "-webkit-box", WebkitLineClamp: 2, WebkitBoxOrient: "vertical", overflow: "hidden", textOverflow: "ellipsis", fontWeight: 600 }}>
                                  {research.researchmethods.map((method, index) => (
                                    <span className="badge badge-secondary mb-2 mr-2">{method.ResearchMethodName}</span>
                                  ))}
                                </div>
                              </div>
                              <div className="col-4 mt-1">
                                {research.researchers.map((researcher, index) => (
                                  <img className="rounded-circle d-inline ml-2 pull-right" style={{ width: "25px" }} src={researcher.ResearcherImage} title={researcher.ResearcherName}></img>
                                ))}
                              </div>
                            </div>
                          </div>
                        </Link>
                      </div>
                    </div>
                  </>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ResearchList;
