import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate, useParams } from "react-router-dom";
import { Heading, Body, Caption, Button } from 'legion-ui';
const nl2br = require('react-nl2br');

const Research = () => {
    const navigate = useNavigate();
    const { id: ResearchID } = useParams();
    const [Research, setResearch] = useState("");
    useEffect(() => {
        getResearchById();
    }, []);

    const getResearchById = async () => {
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/researches/${ResearchID}`);
        setResearch(response.data);
    };
    const deleteResearch = async (ResearchID) => {
        try {
            await axios.delete(`${process.env.REACT_APP_API_URL}/researches/${ResearchID}`);
            navigate("/");
        } catch (error) {
            console.log(error);
        }
    };
    var tags = Research.ResearchTags?.split(",");

    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-9 mt-2">
                    <div className="card">
                        <div className="card-body">
                            <Heading size="h3">{Research.ResearchName}</Heading>

                            <div className="row">
                                <div className="col-12">
                                    <div className="d-flex justify-content-end mt-2">
                                        <div className="mr-auto "><p className="text-muted small mb-0">Tribe: {Research.product?.tribe?.TribeName} ({Research.product?.tribe?.TribeID}) </p></div>
                                        <div className=""><p className="text-muted float-right small  mb-0">Publish Date: {new Date(Research.createdAt).toLocaleDateString("id-ID")}</p></div>
                                    </div>
                                    <div className="d-flex justify-content-end">
                                        <div className="mr-auto "><p className="text-muted small mb-0">Product: {Research.product?.ProductName}</p></div>
                                        <div className=""><p className="text-muted small mb-0">Conduct Date:&nbsp;
                                            {new Date(Research.ResearchStartDate).toLocaleDateString("id-ID")} -&nbsp;
                                            {new Date(Research.ResearchEndDate).toLocaleDateString("id-ID")}</p></div>
                                    </div>
                                </div>
                            </div>
                            <div className="mt-3 text-justify" variant="sm_reguler" dangerouslySetInnerHTML={{ __html: `${Research.ResearchDescription}` }} />
                            <p className="font-weight-bold mb-0">Tags</p>
                            <div id="researchTags">
                                {tags?.map(tag => {
                                    return <a href="#" className="badge badge-primary mr-1">{tag}</a>
                                })}
                            </div>
                            <hr></hr>
                            <Button size="sm" className="d-inline mr-2" onClick={() => navigate(`/edit/${ResearchID}`)} >Edit</Button>
                            <Button size="sm" className="d-inline" onClick={() => deleteResearch(ResearchID)} variant="outline">Delete</Button>
                        </div>
                    </div>
                </div>
                <div className="col-3 nounderline px-0 mt-2">
                    <div className="card">
                        <div className="card-body">

                            {Research.researchlinkdetails?.map((link, index) => (
                                <a href={link.LinkURL} className="panel-hover">
                                    <div className="row p-3" style={{ borderTop: "0.5px solid #ddd" }}>
                                        <div className="col-2 p-0 ">
                                            <img src={link.link.LinkIcon}></img>
                                        </div>
                                        <div className="col-8">
                                            <p>{link.link.LinkName} </p>
                                        </div>
                                        <div className="col-2">
                                            <i className="fa-solid fa-arrow-up-right-from-square" />
                                        </div>
                                    </div>
                                </a>

                            ))}
                        </div>
                    </div>

                    <div className="card mt-2">
                        <div className="card-body">
                            <h4>Research Method</h4>
                            {Research.researchmethods?.map((method, index) => (

                                <a href="#" className="panel-hover">
                                    <div className="row p-3" style={{ borderTop: "0.5px solid #ddd" }}>
                                        <div className="col-2 p-0 ">
                                            <i className={`fa-solid fa-${method.ResearchMethodIcon} text-muted mx-auto fa-2x`}></i>
                                        </div>
                                        <div className="col-8">
                                            <p>{method.ResearchMethodName}</p>
                                        </div>
                                        <div className="col-2">
                                            <i className="fa-solid fa-arrow-up-right-from-square" />
                                        </div>
                                    </div>

                                </a>

                            ))}
                        </div>
                    </div>
                    <div className="card mt-2">
                        <div className="card-body">
                            <h4>Researcher</h4>
                            {Research.researchers?.map((researcher, index) => (
                                <a href={`/researchers/${researcher.ResearcherID}`} className="panel-hover">
                                    <div className="row p-3" style={{ borderTop: "0.5px solid #ddd" }}>
                                        <div className="col-2 p-0 ">
                                            <img className="w-100 rounded-circle" src={`${researcher.ResearcherImage}`}></img>
                                        </div>
                                        <div className="col-8">
                                            <p>{researcher.ResearcherName}</p>
                                        </div>
                                        <div className="col-2">
                                            <i className="fa-solid fa-arrow-up-right-from-square" />
                                        </div>
                                    </div>
                                </a>

                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Research;
